#container {
  padding: 0;
  display: flex;
  flex-direction: column;
  position: absolute;
  overflow-y: auto;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.page {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
}

.section-1 {
  height: 100%;
  min-height: 100%;
  border-bottom: 2px solid #ffffff;
}

.section-2 {
  height: 100%;
  min-height: 100%;
  border-top: 2px solid #ffffff;
}

.section-3 {
  height: 100%;
  min-height: 100%;
  
}

.section-4{
  height: 100%;
  min-height: 100%;
}

.row {
  margin: 0;
}

#SideNavbar-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  padding: 0;
}